    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #0075ff; }
  .person__position {
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2; }
  .person__text {
    line-height: 27px; }

.contact {
  display: flex;
  justify-content: space-between; }
  .contact__form {
    width: 413px; }
  .contact__text {
    display: flex;
    justify-content: space-between;
    width: 413px;
    height: max-content; }
  .contact__info {
    width: 152px;
    margin-bottom: 24px;
    line-height: 27px; }
  .contact__title {
