.footer {
  position: relative;
  height: 359px;
  background-image: url(../images/footer.jpg);
  background-size: cover;
  background-position: center; }
  .footer__logo {
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: auto;
    width: 115px;
    height: 76px; }

body {
  background: #eee; }
